<template>
  <transition name="x-sidebar-animate">
    <div
      class="x-sidebar"
      v-show="show"
    >
      <div class="x-mask" @click="maskClick"></div>
      <div
        class="x-sidebar-content"
        :class="[
          'x-sidebar-' + direction
        ]"
      >
        <slot></slot>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    props: {
      value: {
        type: Boolean,
        default: false
      },
      direction: {
        type: String,
        default: 'left'
      }
    },
    data () {
      return {
        show: this.value
      };
    },
    watch: {
      value (value) {
        if (value !== this.show) this.show = value;
      }
    },
    methods: {
      maskClick () {
        this.show = false;
        this.$emit('input', false);
        this.$emit('close', this.$el);
      }
    }
  };
</script>



