export default {
  props: {
    value: {
      type: [String, Number],
      default: ''
    },
    size: {
      type: String,
      default: 'default'
    },
    type: {
      type: String,
      default: ''
    },
    radius: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String
    },
    max: {
      type: Number
    },
    disabled: {
      type: Boolean,
      default: false
    },
    inline: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      inputValue: this.value
    };
  },
  methods: {
    input (event) {
      let el = event.target;
      this.$emit('input', el.value);
      this.$emit('change', el);
    },
    blur (event) {
      this.$emit('blur', event.target);
    },
    focus (event) {
      this.$emit('focus', event.target);
    }
  },
  watch: {
    value (value) {
      if (value === this.inputValue || this.disabled) return;
      this.inputValue = value;
    }
  }
};



