<template>
  <div
    class="x-radio-group"
    :class="[
      button && 'x-radio-button-group',
      button && `x-radio-button-${size}`,
      button && `x-radio-button-${type}`,
      {
        'x-radio-button-radius': button && radius
      }
    ]"
  >
    <slot></slot>
  </div>  
</template>

<script>
  import porps from '../../mixins/x-checkbox-group_x-radio-group';

  export default {
    name: 'x-radio-group',
    mixins: [porps],
    props: {
      value: {
        type: [String, Number]
      }
    },
    // computed: {
    //   get () {
    //     return this.value;
    //   }
    // },
    methods: {
      change (value) {
        this.$emit('input', value);
      }
    }
  };
</script>



