export default {
  props: {
    data: {
      type: Array,
      default () {
        return [];
      }
    }
  },
  render (h) {
    return (
      <thead>
        <tr>
          {
            this._l(this.data, (item, index) => {
              return (
                <th>{item.renderHead(h)}</th>
              );
            })
          }
        </tr>
      </thead>
    );
  }
};



