/*
  * @Module: tooltip
 * @Author: 小缘
 * @Date: 2017-08-30 22:10:57
 */
import Vue from 'vue';
import tooltip from './x-tooltip-content';
import { on, off, once, clickOutside } from 'utils/dom';

export default {
  props: {
    trigger: {
      type: String,
      default: 'hover'
    },
    direction: {
      type: String,
      default: 'top'
    },
    type: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      tip: '',
      // 延时器
      timeout: '',
      focusEl: ''
    };
  },
  methods: {
    createTip () {
      const Instance = Vue.extend(tooltip);
      let tip = new Instance({
        el: document.createElement('div'),
        data: {
          direction: this.direction,
          type: this.type,
          content: this.$slots.content
        }
      });
      this.tip = tip;
      document.body.appendChild(tip.$el);
      // 绑定事件
      this.trigger === 'hover' && this.tipContentBindEvent();
      return tip;
    },
    // 绑定事件
    bindEvent () {
      const $el = this.$el;
      // 判断内容是否存在
      if (process.env.NODE_ENV !== 'production') {
        if (!this.$slots.content) {
          console.error('xui -> (x-tooltip组件) : slot中的content不能为空');
          return;
        }
      }
      // 事件判断
      switch (this.trigger) {
        case 'click':
          on($el, 'click', this.clickOpen, true);
          break;
        case 'hover':
          on($el, 'mouseenter', this.mouseenterOpen);
          on($el, 'mouseleave', this.mouseleave);
          break;
        case 'focus':
          // 判断是否是input或者textarea
          const el = $el.childNodes[0];
          if (el.nodeName === 'INPUT' || el.nodeName === 'TEXTAREA') {
            this.focusEl = el;
          } else if (el.childNodes.length) {
            const childNodes = el.childNodes || this.focusEl;
            for (let i = 0; i < childNodes.length; i++) {
              const node = childNodes[i];
              const name = node.nodeName;
              if (name === 'INPUT' || name === 'TEXTAREA') {
                this.focusEl = node;
                break;
              }
            }
          }
          if (this.focusEl) {
            on(this.focusEl, 'focus', this.show);
            on(this.focusEl, 'blur', this.hide);
          } else {
            // 执行down
            on($el, 'mousedown', this.mousedownShow);
          }
          break;
      }
    },
    // content绑定
    tipContentBindEvent () {
      const tipEl = this.tip.$el;
      on(tipEl, 'mouseenter', this.mouseenterOpen);
      on(tipEl, 'mouseleave', this.mouseleave);
    },
    // 点击打开
    clickOpen (event) {
      this.show();
      clickOutside(this.tip.$el, this.hide, true);
    },
    // 按下显示
    mousedownShow () {
      this.show();
      once(document, 'mouseup', this.hide);
    },
    mouseenterOpen () {
      this.timeout = clearTimeout(this.timeout);
      this.show();
    },
    // 移出
    mouseleave () {
      this.timeout = setTimeout(() => {
        this.hide();
      }, 50);
    },
    hide () {
      this.tip.show = false;
    },
    show () {
      let tip = this.tip || this.createTip(), left = 0, top = 0;
      const position = this.$el.getBoundingClientRect();
      // 显示
      tip.show = true;
      this.$nextTick(() => {
        const tipPosition = tip.$el.getBoundingClientRect();
        // 方向判断
        switch (this.direction) {
          case 'top':
            left = position.left + (position.width / 2) - (tipPosition.width / 2);
            top = position.top - tipPosition.height;
            break;
          case 'bottom':
            left = position.left + (position.width / 2) - (tipPosition.width / 2);
            top = position.top + position.height;
            break;
          case 'left':
            left = position.left - tipPosition.width;
            top = position.top + (position.height / 2) - (tipPosition.height / 2);
            break;
          case 'right':
            left = position.left + position.width;
            top = position.top + (position.height / 2) - (tipPosition.height / 2);
            break;
        }
        tip.left = left;
        tip.top = top;
      });
    }
  },
  render (h) {
    return (<div class="x-tooltip-box">{this.$slots.default}</div>);
  },
  beforeDestroy () {
    const $el = this.$el;
    // 事件判断
    switch (this.trigger) {
      case 'click':
        off($el, 'click', this.clickOpen, true);
        break;
      case 'hover':
        if (this.tip === '') return;
        const $tipEl = this.tip.$el;
        off($el, 'mouseenter', this.mouseenterOpen);
        off($el, 'mouseleave', this.mouseleave);
        off($tipEl, 'mouseenter', this.mouseenterOpen);
        off($tipEl, 'mouseleave', this.mouseleave);
        break;
      case 'focus':
        if (this.focusEl) {
          off(this.focusEl, 'focus', this.show);
          off(this.focusEl, 'blur', this.hide);
        } else {
          off($el, 'mousedown', this.mousedownShow);
        }
        break;
    }
  },
  mounted () {
    this.bindEvent();
  }
};



