import xTableThead from './x-table-head';
import xTableWidth from './x-table-width.vue';

export default {
  props: {
    data: {
      type: Array,
      default () {
        return [];
      },
      required: true
    },
    bordered: {
      type: Boolean,
      default: false
    },
    striped: {
      type: Boolean,
      default: false
    },
    hover: {
      type: Boolean,
      default: false
    },
    textAlign: {
      type: String,
      default: 'left'
    },
    noBorder: {
      type: Boolean,
      default: false
    },
    height: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      column: []
    };
  },
  render (h) {
    // let fixedHead = this.height ? (
    //   <div class="x-table-header">
    //     <table class="x-table">
    //       <x-table-width list={this.column}></x-table-width>
    //       <x-table-thead data={this.column}></x-table-thead>
    //     </table>
    //   </div>
    // ) : null;
    let tableClass = {
      'x-table-box': true,
      'x-table-bordered': this.bordered,
      'x-table-striped': this.striped,
      'x-table-hover': this.hover,
      'no-border': this.noBorder
    };
    // 文字布局
    tableClass['x-text-' + this.textAlign[0]] = true;
    return (
        <div
          class={tableClass}
        >
          {this.$slots.default}
          <div
            class={{
              'x-table-body': true,
              'x-table-scroll-body': this.height
            }}
          >
            <table class="x-table">
              <x-table-width list={this.column}></x-table-width>
              <x-table-thead data={this.column}></x-table-thead>
              <tbody>
                {
                  this._l(this.data, (row, index) => {
                    return (
                      <tr>
                        {
                          this._l(this.column, (col) => {
                            return (
                              <td>{col.renderContent(h, row, index)}</td>
                            );
                          })
                        }
                      </tr>
                    );
                  })
                }
              </tbody>
            </table>
          </div>
        </div>
    );
  },
  components: {
    xTableThead,
    xTableWidth
  }
};



