/*
  * @Module: toastr提示
 * @Author: 小缘
 * @Date: 2017-07-23 21:13:36
 */
import Vue from 'vue';
import toastrTpl from './x-toastr.vue';

let instance = '';
// 默认配置
const defaultConfig = {
  name: '',
  title: '',
  message: '',
  type: 'primary',
  timer: 5000,
  showIcon: true,
  showClose: true,
  clickBack: '',
  close: ''
};

// 初始化
function initInstance () {
  let InstanceClass = Vue.extend(toastrTpl);
  instance = new InstanceClass({
    el: document.createElement('div')
  });
  // 添加dom
  document.body.appendChild(instance.$el);
}

// 添加
function showToastr (options) {
  Vue.nextTick(() => {
    instance.add(options);
  });
}

// 创建toastr
function createToastr (options, clickBack, close) {
  let config = {};
  const name = options.name || `x-toastr-${Date.now()}`;
  if (process.env.NODE_ENV !== 'production') {
    if (!options.message) {
      console.error('xui -> (x-dialog组件) : message不能为空');
      return;
    }
    // 禁止重复创建
    if (instance === '') {
      initInstance();
    }
    Object.assign(config, defaultConfig, options, {
      clickBack: clickBack,
      close: close,
      name: name
    });
    showToastr(config);
    return () => {
      instance.close(name);
    };
  }
}

class Xtoastr {
  toastr (message, options = {}, clickBack, close) {
    if (process.env.NODE_ENV !== 'production') {
      if (options.type && typeof options.type !== 'string') {
        console.error('xui -> (x-toastr组件) : type只能为String类型');
      }
    }
    options.type = options.type || 'primary';
    if (typeof options === 'function') {
      close = clickBack;
      clickBack = options;
      options = {};
    }
    return createToastr(
      Object.assign({}, {
        message: message
      }, options),
      clickBack,
      close
    );
  }
}

// 关闭函数
Xtoastr.prototype.toastr.close = (name) => {
  if (name && instance) {
    instance.close(name);
  } else {
    instance.close();
  }
};

export default new Xtoastr();



