<template>
  <transition name="x-toastr-let-move">
    <div
      class="x-toastr"
      :class="[
        'x-toastr-' + options.type,
        {
          'x-toastr-show-icon': options.showIcon,
          'x-toastr-show-close': options.showClose
        }
      ]"
      @click="toastrClick"
    >
      <div class="x-toastr-content">
        <i
          class="x-toastr-icon"
          :class="options.showIcon && type"
        ></i>
        <div class="x-toastr-title" v-if="options.title">{{options.title}}</div>
        {{options.message}}
        <i
          class="x-toastr-close x-icon-close-round"
          v-if="options.showClose"
          @click.stop="close"
        ></i>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    props: {
      options: {
        type: Object
      },
      type: {
        type: String
      }
    },
    data () {
      return {
        timeout: ''
      };
    },
    methods: {
      countdown () {
        this.timeout = setTimeout(() => {
          // 关闭
          this.close();
        }, this.options.timer);
      },
      // 关闭
      close () {
        this.clearTimeout();
        // 移除
        this.$parent.close(this.options.name);
      },
      toastrClick () {
        const clickBack = this.options.clickBack;
        clickBack && clickBack(this);
      },
      clearTimeout () {
        // 组建销毁的时候重复调用
        if (this.timeout) {
          const close = this.options.close;
          close && close();
          clearTimeout(this.timeout);
          this.timeout = '';
        }
      }
    },
    mounted () {
      // 判断是否启用定时器
      if (typeof this.options.timer === 'number' && this.options.timer !== 0) {
        this.countdown();
      }
    },
    beforeDestroy () {
      // 销毁定时器
      this.clearTimeout();
    }
  };
</script>




