import { parent } from '../utils/query-components';

export default {
  name: 'x-checkbox',
  props: {
    value: {},
    type: {
      type: String,
      default: 'primary'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    label: {
      type: [String, Number]
    },
    checked: {
      type: Boolean,
      default: false
    },
    name: {
      type: String
    }
  },
  data () {
    return {
      model: '',
      state: this.value,
      isDisabled: '',
      parent: ''
    };
  },
  methods: {
    change (e) {
      const checked = e.target.checked;
      if (this.parent) {
        this.parent.change(this.label);
      } else {
        this.updateState(checked);
        this.emitValue();
        this.$emit('change', e);
      }
    },
    // 更新状态
    updateState (val) {
      this.state = val ? this.label || true : false;
    },
    // 同步model
    emitValue () {
      this.$emit('input', this.state);
    }
  },
  mounted () {
    this.parent = parent(this, 'x-checkbox-group');
    // 禁用状态判断
    // this.isDisabled = this.parent ? this.disabled || this.parent.disabled : this.disabled;
    // 判断是否默认选中
    if (this.checked) {
      this.parent ? this.parent.change(this.label, true) : this.emitValue();
      this.updateState(true);
    }
  }
};



