<template>
  <div
    class="x-toastr-box"
  >
    <x-toastr
      v-for="(item, index) in toastrList"
      :key="item.name"
      :type="icons[item.type]"
      :options="item"
    ></x-toastr>
  </div>
</template>

<script>
  import xToastr from './x-toastr-item.vue';
  export default {
    data () {
      return {
        toastrList: [],
        icons: {
          success: 'x-icon-checkmark-circle',
          warning: 'x-icon-alert',
          danger: 'x-icon-cancel',
          primary: 'x-icon-information-circled',
          dark: 'x-icon-hangout'
        },
        intervalList: []
      };
    },
    methods: {
      add (options) {
        const length = this.toastrList.length;
        this.$set(this.toastrList, length, options);
      },
      close (name) {
        // name如果不存在则关闭全部
        if (typeof name === 'string' || typeof name === 'number') {
          const list = this.toastrList;
          for (let i = 0; i < list.length; i++) {
            if (list[i].name === name) {
              // 移除
              list.splice(i, 1);
              break;
            }
          }
        } else {
          this.toastrList = [];
        }
      }
    },
    components: {
      xToastr
    }
  };
</script>




