<template>
  <transition name="bounce">
    <div
      class="x-dialog-box"
      v-show="show"
    >
      <!--遮罩-->
      <div class="x-mask" @click="maskClick"></div>
      <div
        class="x-dialog"
        :class="{
          'x-radius': radius
        }"
      >
        <!--标题-->
        <div class="x-dialog-title">{{title}}</div>
        <span
          class="x-dialog-close"
          @click.stop="buttonClick('close')"
        >
          <i class="x-icon-close-round"></i>
        </span>
        <!--内容-->
        <div class="x-dialog-content">
          <x-input v-if="type === 'prompt'" v-model="inputValue" :placeholder="inputPlaceholder"></x-input>
          <span v-else>{{message}}</span>
        </div>
        <!--按钮内容-->
        <div class="x-dialog-footer x-text-r">
          <x-button
            v-if="showCloseButton"
            size="small"
            @click="buttonClick('close')"
          >{{closeButtonText}}</x-button>
          <x-button
            v-if="confirmButtonText"
            size="small"
            type="primary"
            @click="buttonClick('confirm')"
          >{{confirmButtonText}}</x-button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import xButton from '../x-button/x-button.vue';
  import xInput from '../x-input/x-input.vue';

  export default {
    components: {
      xButton,
      xInput
    },
    data () {
      return {
        show: '',
        title: '',
        type: 'alert',
        message: '',
        inputValue: '',
        radius: '',
        inputPlaceholder: '',
        showCloseButton: '',
        showConfirmButton: '',
        closeButtonText: '',
        confirmButtonText: '',
        close: '',
        confirm: '',
        maskClose: '',
        promptRequired: ''
      };
    },
    methods: {
      buttonClick (type) {
        let params = {
          el: this.$el
        };
        switch (type) {
          case 'close':
            this.close(params);
            this.hide();
            break;
          case 'confirm':
            if (this.type === 'prompt') {
              params.value = this.inputValue;
            }
            // 是否必填
            if (this.promptRequired && this.inputValue.replace(/(^\s*)|(\s*$)/g, '') === '') {
              return;
            }
            this.hide();
            this.confirm(params);
            break;
        }
      },
      hide () {
        // 移除遮罩
        this.show = false;
      },
      maskClick () {
        if (!this.maskClose) return;
        this.buttonClick('close');
      }
    }
  };
</script>



